.containercard {
    /* padding: 5rem 2rem 6rem; */
    /* padding-left: 10% ; */
    position: relative;
    min-height: 200px;
    z-index: 1;
    width: 100%;
    overflow: hidden;
    max-height: 900px;
    cursor: pointer;
    margin : 0px !important;
    /* border: #5a94ff;
    border-width: 5px; */
    /* background-color: 57a6f1;
     */
    
}
.containercard::before,
.containercard::after{
    content: ' ';
    display: block;
    position: absolute;
    width: 0px;
    height: 0px;
    border-radius: 63% 37% 16% 84% / 60% 54% 46% 40% ;
    -webkit-border-radius: 63% 37% 16% 84% / 60% 54% 46% 40% ;
    -moz-border-radius: 63% 37% 16% 84% / 60% 54% 46% 40% ;
    -ms-border-radius: 63% 37% 16% 84% / 60% 54% 46% 40% ;
    -o-border-radius: 63% 37% 16% 84% / 60% 54% 46% 40% ;
  background: linear-gradient(140deg,whitesmoke,#ffffff);
    right:40%;
    top: 0;
    transform: translate(20%, 0%);
    -webkit-transform: translate(20%, 0%);
    -moz-transform: translate(20%, 0%);
    -ms-transform: translate(20%, 0%);
    -o-transform: translate(20%, 0%);
} 
.containercard::after {
    z-index: -1;
    top: 50%;
    right: 50%;
    border-radius: 59% 41% 60% 40% / 69% 50% 50% 31%  ;
    -webkit-border-radius: 59% 41% 60% 40% / 69% 50% 50% 31%  ;
    -moz-border-radius: 59% 41% 60% 40% / 69% 50% 50% 31%  ;
    -ms-border-radius: 59% 41% 60% 40% / 69% 50% 50% 31%  ;
    -o-border-radius: 59% 41% 60% 40% / 69% 50% 50% 31%  ;
    background: linear-gradient(140deg, whitesmoke, white);
}
.cart {
    width: 100%;
    position: relative;
    box-shadow: 0 0 3rem #d1efff;
    border: 1px solid #fff2;
    backdrop-filter: blur(15px);
    border-radius: 1rem;
    -webkit-border-radius: 1rem;
    -moz-border-radius: 1rem;
    -ms-border-radius: 1rem;
    -o-border-radius: 1rem;
    padding: .8rem 1rem;
    color: white;
    background: linear-gradient(8deg, #fff0, #fff2, #fff3);
}
@media screen and (min-width:576px) {
    .containercard {
        max-width: 100%;
        overflow: visible;
    }
