.productColorsWrapper {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    margin-top: 1.5rem;
}
.productColorLable {
    position: relative;
    display: block;
    background-color: #FA8F6B;
    cursor: pointer;
    width: 28px;
    height: 28px;
    margin-right: 1rem;
    border-radius: .2rem;
    -webkit-border-radius: .2rem;
    -moz-border-radius: .2rem;
    -ms-border-radius: .2rem;
    -o-border-radius: .2rem;
}
.productColorLable:nth-of-type(3) {
    background-color: #ff5c43;
}
.productColorLable:nth-of-type(4) {
    background-color: #04dc70;
}
.productColorLable:nth-of-type(2) {
    background-color: #d045ff;
}
.productColorLable:nth-of-type(1) {
    background-color: #5a94ff;
}
.product_color {
    position: absolute;
    pointer-events: none;
    opacity: 0;
    clip: rect(0, 0, 0, 0);
    visibility: hidden;
}

.product_color + span {
    position: absolute;
    width: 38px;
    height: 38px;
    top: 50%;
    left: 50%;
    border-radius: inherit;
    border: 1px solid #0005;
    -webkit-border-radius: inherit;
    -moz-border-radius: inherit;
    -ms-border-radius: inherit;
    -o-border-radius: inherit;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transition: opacity .15s linear 0s;
    -webkit-transition: opacity .15s linear 0s;
    -moz-transition: opacity .15s linear 0s;
    -ms-transition: opacity .15s linear 0s;
    -o-transition: opacity .15s linear 0s;
    opacity: 0;
}
.product_color:checked + span {
    opacity: 1;
}
.addToCartBtn {
    display: block;
    border-radius: .5rem;
    text-align: center;
    margin: 2rem 0 0;
    line-height: 3;
    text-decoration: none;
    font-weight: bold;
    color: white;
    -webkit-border-radius: .5rem;
    -moz-border-radius: .5rem;
    -ms-border-radius: .5rem;
    -o-border-radius: .5rem;
    border: 1px solid #fff2;
    background: linear-gradient(8deg, #fff0, #fff2, #fff3);
    box-shadow: 0 0 1rem #00000020;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 1;
}
.addToCartBtn::before {
    content: ' ';
    position: absolute;
    display: block;
    width: 100%;
    height: 60%;
    background-color: #ffffff10;
    top: 0;
    border-radius: inherit;
    -webkit-border-radius: inherit;
    -moz-border-radius: inherit;
    -ms-border-radius: inherit;
    -o-border-radius: inherit;
    z-index: -1;    
}